<template>
  <div class="search">
    <div class="search-header">
      <van-row>
        <van-col span="2">
          <van-icon name="arrow-left" @click="$router.push({name:'homepage'})" />
        </van-col>
        <van-col span="18">
          <van-search v-model="value" shape="round" />
        </van-col>
        <van-col span="4">
          <van-button type="danger" size="small" round @click="search">搜索</van-button>
        </van-col>
      </van-row>
    </div>

    <!-- 二级路由插座 -->
    <router-view />
  </div>
</template>

<script>
export default {
  data() {
    return {
      value: "",
      values:" ",
    };
  },
  methods: {
    search() {
      if (this.value.length < 1) {
        return;
      }
      if(this.value == this.values){
        return;
      }
      this.values = this.value
      this.$router.push({ name: "searchkeywords", query: this.value });
    },
    
  },
  watch: {
    $route(to) {
      // console.log(to.query);
      this.value = to.query
    }
  },
  computed: {},
  created() {
    //隐藏导航
    this.$store.state.vantTab = false;
    // console.log(this.$route.query.value);
    if (this.$route.query.value != undefined) {
      this.value = this.$route.query.value;
      this.search();
    }
  },
  // 切换组件生周期钩子，销毁函数
  beforeDestroy() {
    // this.$store.state.vantTab = true;
    this.value = "";
  }
};
</script>

<style lang="less" scoped>
.search {
  height: 100%;
  padding-top: 50px;
  .search-header {
    background-color: #ffffff;
    height: 54px;
    width: 100%;
    position: fixed;
    left: 0;
    top: 0;
    z-index: 1;
    .van-col {
      position: relative;
      height: 100%;
      text-align: center;
      .van-icon {
        margin-top: 11px;
        width: 30px;
        height: 30px;
        font-size: 30px;
      }
      button {
        margin-top: 11px;
      }
    }
  }
}
</style>